
import { defineComponent } from "vue";

export default defineComponent({
  name: "Error",
  data() {
    return {
      statusCode: NaN,
    };
  },
  created() {
    this.statusCode = parseInt(this.$route.params.statusCode as string);
  },
  beforeRouteUpdate(to, from) {
    // react to route changes
    this.statusCode = parseInt(this.$route.params.statusCode as string);
  },
});
